import React from "react";
import styled from 'styled-components';
import SolarPergolaImg from '../assets/homepage/solar-pergola.jpg'
import PowerPergolaImg from '../assets/powerpergola/power-pergola.jpg'
import PowerSolaireImg from '../assets/powerpergola/power-solaire.jpg'
import SafeImg from '../assets/powerroof/safe.jpg';
import SpectacularImg from '../assets/powerpergola/spectacular.jpg';
import ReliableImg from '../assets/powerroof/reliable.jpg';
import { Parallax } from "react-parallax";
import Fade from 'react-reveal/Fade';
import Grid from '@mui/material/Grid';
import Footer from "../components/Footer";
import { animateScroll as scroll } from 'react-scroll';
import ScrollToTopIcon from '../components/ScrollToTopIcon';
import BannerVideo from "../assets/powerpergola/power-pergola-banner.mp4";
import Navbar from "../components/Navbar";
// import VrpanoIcon from '@mui/icons-material/Visibility';
import Tooltip from '@mui/material/Tooltip';
import Modal from '@mui/material/Modal';
import ARIconImage from '../assets/icons/ar-icon.png';
import PergolaModel from '../assets/powerpergola/ar/pergola.glb';
import PergolaPoster from '../assets/powerpergola/ar/pergola.png';
import SolaireModel from '../assets/powerpergola/ar/solaire.glb';
import SolairePoster from '../assets/powerpergola/ar/solaire.png';
import { LazyLoadComponent } from 'react-lazy-load-image-component';
import axios from 'axios';
import { ReactMarkdown } from "react-markdown/lib/react-markdown";

class PowerPergola extends React.Component {
  state = {
    hide: false,
    activeTab: -1,
    scroll: false,
    bottom: false,
    modalOpen: false,
    model: 0,
    content: {},
  }
  rootRef = React.createRef();
  variantsRef = React.createRef();
  safeRef = React.createRef();
  spectacularRef = React.createRef();
  reliableRef = React.createRef();
  _isMounted = false

  componentDidMount() {
    this._isMounted = true;
    axios.get('https://arkacms.xyz/api/mw-power-pergola-pages')
      .then(res => {
        // console.log(res.data);
        let data = [...res.data.data];
        let content = {};
        data.forEach(item => {
          content[item?.attributes?.label] = item?.attributes?.richContent || item?.attributes?.content;
        });
        this.setState({ content: { ...content } });
        // console.log(content)
      })
      .catch(err => {
        console.log('Error : ', err)
      });
    scroll.scrollToTop({
      duration: 400,
      smooth: 'easeInOut'
    });

    setTimeout(() => {
      let sections = [this.variantsRef, this.safeRef, this.spectacularRef, this.reliableRef]
      let anyVisible = false;
      sections.forEach((ref, index) => {
        if (ref.current) {
          var rect = ref.current.getBoundingClientRect();
          var visible = (rect.top > -1 && rect.top < window.innerHeight / 2) || (rect.bottom > window.innerHeight / 2 && rect.bottom - window.innerHeight < window.innerHeight / 2);
          if (visible) {
            anyVisible = true;
            this.setState({ activeTab: index });
          }
        }
      });
      if (!anyVisible) {
        this.setState({ activeTab: -1 });
      }
      window.addEventListener('scroll', this.onScroll, false);
    }, 500)
  }
  onScroll = (e) => {
    if (window.scrollY > 350) {
      this.setState({ hide: true });
    } else {
      this.setState({ hide: false });
    }
    if (window.scrollY > window.innerHeight * 1.5) {
      let sections = [this.variantsRef, this.safeRef, this.spectacularRef, this.reliableRef]
      let anyVisible = false
      sections.forEach((ref, index) => {
        if (ref.current) {
          var rect = ref.current.getBoundingClientRect();
          var visible = (rect.top > -1 && rect.top < window.innerHeight / 2) || (rect.bottom > window.innerHeight / 2 && rect.bottom - window.innerHeight < window.innerHeight / 2);
          if (visible) {
            anyVisible = true;
            this.setState({ activeTab: index });
          }
        }
      });
      if (!anyVisible) {
        this.setState({ activeTab: -1 });
      }
    }

    // let that = this;
    // var scroll = window.scrollY;
    // if (scroll > 100 && that._isMounted) that.setState({ scroll: true });
    // else if (that._isMounted) that.setState({ scroll: false });
    // if (((window.innerHeight + window.pageYOffset + 50) >= document.body.offsetHeight) && that._isMounted)
    //   that.setState({ bottom: true })
    // else if (that._isMounted)
    //   that.setState({ bottom: false })
  }
  componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener('scroll', this.onScroll, false);
  }
  render() {
    let content = this.state.content;
    return (
      <Wrapper ref={this.rootRef}>

        <Navbar containerRef={this.rootRef} position="fixed" hide={this.state.hide} />

        <Parallax
          bgImage={null}
          bgImageAlt="solar roof"
          strength={300}
          renderLayer={percentage => (
            <div ref={this.bannerRef} style={{ minHeight: "150vh", background: 'rgba(0,0,0,0.4)', position: 'relative' }}>
              <LazyLoadComponent>
                <HeroVideo poster={SolarPergolaImg} loop playsInline autoPlay muted style={{ width: 'auto', height: '150vh', zIndex: '-1', mixBlendMode: 'multiply', position: 'absolute' }}>
                  <source src={BannerVideo} type="video/mp4" />
                </HeroVideo>
              </LazyLoadComponent>
              <Banner style={{ marginTop: `${(percentage - 1) * 45}%` }}>
                <Fade left duration={1000}>
                  <FlexContainer style={{ width: 'max-content', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '4rem', margin: '2rem 0' }}>{content?.heroText}</FlexContainer>
                </Fade>
                <Fade opposite bottom delay={500} duration={500}>
                  <FlexContainer style={{ width: '30rem', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '1.75rem', lineHeight: '2' }}>{content?.heroSubtext}</FlexContainer>
                </Fade>
                <Fade opposite delay={1000} duration={1000}>
                  <PaddedText>
                    <YOButton>
                      {content?.heroButtonText}
                    </YOButton>
                  </PaddedText>
                </Fade>
              </Banner>
            </div>
          )}
        >
        </Parallax>

        <Bar>
          <div style={{ background: 'white', color: 'black', padding: '0 2rem', display: 'inline-flex', width: 'max-content', height: '100%', alignItems: 'center', justifyContent: 'center' }}>
            <b>POWER PERGOLA</b>
          </div>
          <div style={{ display: 'inline-flex', width: 'auto', padding: '0 2rem', height: '100%', alignItems: 'center', position: 'relative' }}>
            <NavLink onClick={() => { this.setState({ activeTab: 0 }); this.variantsRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 0)}>
              Variants
            </NavLink>
            <NavLink onClick={() => { this.setState({ activeTab: 1 }); this.safeRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 1)}>
              Safe
            </NavLink>
            <NavLink onClick={() => { this.setState({ activeTab: 2 }); this.spectacularRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 2)}>
              Spectacular
            </NavLink>
            <NavLink onClick={() => { this.setState({ activeTab: 3 }); this.reliableRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 3)}>
              Reliable
            </NavLink>
          </div>
        </Bar>

        <div style={{ height: 'maxContent', background: '#002040' }} ref={this.variantsRef}>
          <Grid container style={{ color: 'white' /*, flexWrap: 'wrap-reverse'*/, height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6} style={{ borderRight: '2px solid white' }}>
              <div style={{ fontSize: '2.75rem', fontWeight: '300', width: '80%', padding: '4rem 10% 1rem', display: 'flex', alignItems: 'center' }}>
                <Fade bottom delay={250} duration={250}>
                  <>
                    {content?.powerPergolaTitle}
                    <Tooltip title="View in 3D" placement="bottom" arrow>
                      {/* <VrpanoIcon onClick={() => { this.setState({ modalOpen: true, model: 1 }) }} className='hover-icon' style={{ marginLeft: '1rem', fontSize: '2.25rem' }} /> */}
                      <ARIcon
                        src={ARIconImage}
                        onClick={() => { this.setState({ modalOpen: true, model: 1 }) }}
                      />
                    </Tooltip>
                  </>
                </Fade>
              </div>
              <div style={{ fontSize: '1rem', fontWeight: '300', width: '80%', padding: '0 10%', marginBottom: '2rem' }}>
                <Fade bottom delay={750} duration={250}>
                  {content?.powerPergolaDescription}
                </Fade>
              </div>
              <div>
                <Fade bottom delay={1000} duration={250}>
                  <LazyLoadComponent>
                    <img src={PowerPergolaImg} alt="Power Pergola" style={{ width: '100%', height: '80%', marginBottom: '3rem' }} />
                  </LazyLoadComponent>
                </Fade>
              </div>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6} style={{ borderLeft: '2px solid white' }}>
              <div style={{ fontSize: '2.75rem', fontWeight: '300', width: '80%', lineHeight: '3rem', padding: '4rem 10% 1rem', display: 'flex', alignItems: 'center' }}>
                <Fade bottom delay={250} duration={250}>
                  <>
                    {content?.powerSolaireTitle}
                    <Tooltip title="View in 3D" placement="bottom" arrow>
                      {/* <VrpanoIcon onClick={() => { this.setState({ modalOpen: true, model: 2 }) }} className='hover-icon' style={{ marginLeft: '1rem', fontSize: '2.25rem' }} /> */}
                      <ARIcon
                        src={ARIconImage}
                        onClick={() => { this.setState({ modalOpen: true, model: 1 }) }}
                      />
                    </Tooltip>
                  </>
                </Fade>
              </div>
              <div style={{ fontSize: '1rem', fontWeight: '300', width: '80%', padding: '0 10%', marginBottom: '2rem' }}>
                <Fade bottom delay={750} duration={250}>
                  {content?.powerSolaireDescription}
                </Fade>
              </div>
              <div>
                <Fade bottom delay={1000} duration={250}>
                  <LazyLoadComponent>
                    <img src={PowerSolaireImg} alt="Power Solaire" style={{ width: '100%', height: '80%', marginBottom: '3rem' }} />
                  </LazyLoadComponent>
                </Fade>
              </div>
            </Grid>
          </Grid>
        </div>


        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.safeRef}>
          <Grid container style={{ color: 'white' /*, flexWrap: 'wrap-reverse'*/, height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionOneHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <div>
                      <ReactMarkdown escapeHTML={false} >
                        {content?.sectionOneContent}
                      </ReactMarkdown>
                    </div>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={SafeImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
          </Grid>
        </div>

        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.spectacularRef}>
          <Grid container style={{ color: 'white', flexWrap: 'wrap-reverse', height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={SpectacularImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionTwoHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <div>
                      <ReactMarkdown escapeHTML={false} >
                        {content?.sectionTwoContent}
                      </ReactMarkdown>
                    </div>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
          </Grid>
        </div>

        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.reliableRef}>
          <Grid container style={{ color: 'white' /*, flexWrap: 'wrap-reverse'*/, height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionThreeHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <div>
                      <ReactMarkdown escapeHTML={false} >
                        {content?.sectionThreeContent}
                      </ReactMarkdown>
                    </div>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={ReliableImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
          </Grid>
        </div>

        <Footer rf={this.exploreRef} />

        <ScrollToTopIcon scroll={this.state.scroll} bottom={this.state.bottom} />

        <Modal open={this.state.modalOpen} onClose={() => this.setState({ modalOpen: false })}>
          <div style={{ backgroundColor: 'white', borderRadius: '1rem', height: '90vh', width: '90vw', padding: '0', display: 'flex', alignItems: 'center', justifyContent: 'center', outline: 'none !important', margin: '5vh 5vw' }}>
            <model-viewer
              src={this.state.model === 1 ? PergolaModel : SolaireModel}
              poster={this.state.model === 2 ? PergolaPoster : SolairePoster}
              camera-controls alt="A 3D model carousel"
              shadow-intensity="1"
              ar ar-modes="scene-viewer quick-look"
              style={{ height: '100%', width: '100%', textAlign: 'center' }} >

              <MobileOnly>
                <button slot="ar-button" id="ar-button">
                  Check it out in your own space!
                </button>
                <br /><br />

                <div id="ar-prompt">
                  <img src={require('../assets/powerpergola/ar/hand.png')} alt='Hand' />
                </div>
                <br /><br />

                <button id="ar-failure">
                  Please use a spacious outdoor environment to view the model properly
                </button>
              </MobileOnly>

            </model-viewer>
          </div>
        </Modal>
      </Wrapper >
    );
  }
}

export default PowerPergola;

const Wrapper = styled.div`
  width: 100%;
`;

const Bar = styled.nav`    
  top: 0;
  z-index:200;
  position:sticky;

  height:3rem;
  width:100%;
  padding:0;
  
  background: linear-gradient(45deg, #000 0%, #024 100%);  
  transition:all 0.5s ease-in-out;
  
  box-shadow:0 4px 4px 0 rgba(0,0,0,0.6);
  display: flex;
`;

const ARIcon = styled.img`
  background-color: #fff;
  border-radius: 0.5rem;
  padding: 0rem;
  margin-left: 2rem;
  transition: 0.3s all ease-in-out;
  height: 2.5rem;
  border: 1px solid #ccc;
  box-shadow: 0px 0px 5px 2px rgba(255,255,255,0.5);
  margin-bottom: -0.25rem;
  cursor: pointer;
  &:hover{
    box-shadow: 0px 0px 5px 2px orange;
    background-color: yellow;
  }
`;

const NavLink = styled.div`
  margin:0 1rem;
  padding:0.5rem 0;
  position:relative;
  outline: none;
  
  color:white;
  font-size: 1rem;
  font-weight: 500;
  text-decoration:none;
  
  transition:all 0.3s ease-in-out;
  cursor:pointer;

  &:after{
    content: '';
    position:absolute;
    left:0;
    bottom:0.25rem;
    height:${props => props.active === 'true' ? '2px' : '0.5px'};
    width:${props => props.active === 'true' ? '100%' : '0'};
    margin-left:${props => props.active === 'true' ? '0' : '50%'};
    transition: all 0.1s ease;
    background-color: ${props => props.active === 'true' ? 'orange' : 'orange'};
  }

  &:hover{
    color:orange;
    &:after{
      width:100%;
      margin-left: 0;
      box-shadow:${props => props.active === 'true' ? 'none' : '2px 2px 2px 0px rgba(255,255,255,0.8)'};
    }
  }
`;

const MobileOnly = styled.div`
  display: none;
  @media only screen and (max-width : 768px) {
    display: block;
  } 
`

const HeroVideo = styled.video`
  width: auto;
  height: 150vh;
  z-index: -1;
  mix-blend-mode: multiply;
  position: absolute;
  
  @media only screen and (max-width : 1260px) {
    left: -20vw;
  }  
  @media only screen and (max-width : 900px) {
    left: -60vw;
  }
  @media only screen and (max-width : 600px) {
    left: -200vw;
  }
`;

const Banner = styled.div`
  padding: 3rem 5vw;
  position: absolute;
  width: 90vw;
  top: 35%;
  left: 50%;
  transform: translate(-50%,-35%);
  color: white;
`;

const FlexContainer = styled.div`
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-direction: column;
`;

const PaddedText = styled.div`
  padding: 1rem 0;
  line-height: 2;
`;

const ImageContainer = styled.div`
  background:red;
  background: url(${props => props.src});
  margin:0;
  display: flex;
  width: 100%;
  height: 100vh !important;
  background-size: cover;
  background-position: center;
`;

const YOButton = styled.button`
  background: #0007;
  color: orange;
  border: 2px solid orange;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  z-index: 100;
  transition: all 0.3s ease;
  cursor: pointer;

  &:hover {
    background: orange;
    color: black;
  }
`;